import React from 'react';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware, combineReducers } from 'redux';
import { searchRobots, requestRobots } from './reducers';
import { createLogger } from 'redux-logger';
import thunkMiddleware from 'redux-thunk';
import ReactDOM from 'react-dom'; // the little robot that handles the dom/the glue of web
import './index.css'; //React allows css customization to individual components
import registerServiceWorker from './registerServiceWorker'; //allows faster apps and potential  to work offline
import 'tachyons';
import App from './container/App';


const rootReducer = combineReducers({searchRobots, requestRobots});

const logger = createLogger();
const store = createStore(rootReducer, applyMiddleware(thunkMiddleware, logger));

ReactDOM.render(
	<Provider store={store}>
		<App/>
	</Provider>,
	document.getElementById('root'));
registerServiceWorker();



